import { getComments } from "/request/api.js";
export default {
  name: "article-comments",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  async mounted() {
    const { data } = await getComments(this.article.slug);
    this.comments = data.comments;
  },
  data() {
    return {
      comments: [], //文章列表
    };
  },
  methods: {},
};
